import { Injectable } from '@angular/core';
import { GlobalData } from "./GlobalData";
declare var fundebug;

/**
 * Utils
 * @description
 */
@Injectable()
export class Logger {
  constructor(private globalData: GlobalData) {
  }

  log(err: any, action: string, other = null): void {
    console.log('Logger.logaction-' + action);
    other && console.log(other);
    console.log(err);
    fundebug.notifyError(err,
      {
        metaData: {
          action: action, // 
          other: other, // 
          user: {id: this.globalData.Uid, name: this.globalData.Name}
        }
      });
  }

  httpLog(err: any, msg: string, other = {}): void {
    console.log('Logger.httpLogmsg-' + msg);
    fundebug.notifyHttpError(err,
      {
        metaData: {
          action: msg, // 
          other: other, // 
          user: {id: this.globalData.Uid, name: this.globalData.Name}
        }
      });
  }



}


